import React, { Component, useState } from "react"
import { Link } from "gatsby"

import { Parallax, ParallaxLayer } from 'react-spring/renderprops-addons'
import { animated, useSpring } from 'react-spring'
import Clipboard from 'react-clipboard.js';

import Title from '../components/title.js'
import Services from '../components/services.js'
import About from '../components/about.js'
import Skills from '../components/skills.js'
import SEO from '../components/seo.js'

import './typography.css'
import './index.css'
import './about.css'
import './mobile.css'


const MobileNav = () => {

  const [copySuccess, setCopySuccess] = useState(false);

    const stylin = useSpring({
    to: async (next, cancel) => {
      await next({opacity: copySuccess ? 1 : 0})
      await next({opacity: 0})
    },
    from: {opacity: 0}
  })


  return(

          <ParallaxLayer offset={0} speed={-1}>
            <section>
              <div class="wrap-100 bottom-center">
                <Clipboard onClick={() => setCopySuccess(state => !state)} data-clipboard-text="jeffgsch@gmail.com">
                  <animated.p style={stylin}>copied</animated.p>
                  <p class="mobile-text padding nav-bk">jeffsgsch@gmail.com</p>
                </Clipboard>
              </div>
            </section>
          </ParallaxLayer>      

    )

}


const WebNav = (props) => {

  const [copySuccess, setCopySuccess] = useState(false);

    const stylin = useSpring({
    to: async (next, cancel) => {
      await next({opacity: copySuccess ? 1 : 0})
      await next({opacity: 0})
    },
    from: {opacity: 0}
  })

  return(

          <ParallaxLayer offset={0} speed={-1}>
            <section>
              <div class="wrap-75 display-apart">
                <div>
                <p class="cursor" onClick={props.to_top}>to top</p>
                <p class="cursor" onClick={props.to_bottom}>to bottom</p>
                </div>
                <div>
                  <Clipboard onClick={() => setCopySuccess(state => !state)} data-clipboard-text="jeffgsch@gmail.com">
                    <animated.p style={stylin}>copied</animated.p>
                    <p class="nav-bk padding">jeffgsch@gmail.com</p>
                  </Clipboard>
                </div>
              </div>
            </section>
          </ParallaxLayer>      

    )

}

class IndexPage extends Component {

  constructor(props) {
    super(props);
    this.state = {
      w: null,
      h: null,
      mounted: false,
      active: 0,
      slides: [
        'front',
        'about',
        'services',
        'skills',
        'contact'
      ]
    }
    this.handleResize = this.handleResize.bind(this);
  }

  componentDidMount() {
    window.addEventListener("resize", this.handleResize);

    let w = window.innerWidth;
    let h = window.innerHeight;

    this.setState({
      w: w,
      h: h,
      mounted: true
    })
  }

  handleResize() {
    let w = window.innerWidth;
    let h = window.innerHeight;

    this.setState({
      w: w,
      h: h
    })
  }


  render() {

    let w = this.state.w;
    let mobile;

    if ( w < 1000 ) {
      mobile =  true;
    } else {
      mobile = false;
    }


    return(
      <React.Fragment>

        <SEO />
        <Parallax pages={8} ref={ref => (this.parallax = ref)}>
          <Title />
          <Services />
          <Skills />
          <About />

          {
            mobile ?  <MobileNav /> : <WebNav to_top={() => this.parallax.scrollTo(0)} to_bottom={() => this.parallax.scrollTo(7)} /> 
          }

        </Parallax>
      </React.Fragment>
      )
  }

}

export default IndexPage
