import React, { Component, useState } from "react"
import { Link } from "gatsby"

import { Parallax, ParallaxLayer } from 'react-spring/renderprops-addons'
import { animated, useSpring } from 'react-spring'

import Profile from '../images/profile.jpg'

const About = (props) => {

	return(
		<React.Fragment>
		<ParallaxLayer offset={6} speed={.6}>
			<section>
				<animated.div class="wrap-75 center black-bk">
					
				</animated.div>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={7} speed={.6}>
			<section>
				<animated.div class="wrap-50 center black-bk">
					
				</animated.div>
			</section>
		</ParallaxLayer>

		<ParallaxLayer offset={6} speed={0.6}>
			<section>
				<div class="wrap-75 center">
					<img class="bk-img" src={Profile} />
				</div>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={6} speed={0.5}>
			<section>
				<div class="wrap-75 center">
					<img src={Profile} />
				</div>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={5} speed={0.2}>
			<section>
				<div class="wrap-75 center column">
					<h2 class="text-center">about me</h2>
					<h3 class="text-center color-grey">aka</h3>
					<h2 class="text-center">hacker supreme</h2>
					<h3 class="text-center color-grey">aka</h3>
					<h2 class="text-center">jeff schwartz</h2>
				</div>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={5.75} speed={0.4}>
			<section>
				<div class="wrap-100 center column">
					<p class="text-center ">Based out of West Michigan</p>
					<p class="text-center">University of Michigan Grad</p>
					<p class="text-center">2ish Years Experience</p>
				</div>
			</section>
		</ParallaxLayer>

		<ParallaxLayer offset={6.75} speed={0.4}>
			<section>
				<div class="wrap-100 center column">
					<p class="text-center ">INTJ</p>
					<p class="text-center">Picses</p>
					<p class="text-center">Authorized to work in the united states</p>
				</div>
			</section>
		</ParallaxLayer>

		<ParallaxLayer offset={7} speed={0.2}>
			<section>
				<div class="wrap-75 center column">
					<h3 class="text-center">Reach out to me about my projects!</h3>
				</div>
			</section>
		</ParallaxLayer>


		</React.Fragment>
		)
}

export default About