import React, { Component, useState } from "react"
import { Link } from "gatsby"

import { Parallax, ParallaxLayer } from 'react-spring/renderprops-addons'
import { animated, useSpring } from 'react-spring'


const Services = (props) => {

	return(
		<React.Fragment>
		<ParallaxLayer offset={1} speed={.6}>
			<section>
				<animated.div class="wrap-75 center black-bk">
					
				</animated.div>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={2} speed={.6}>
			<section>
				<animated.div class="wrap-75 center black-bk">
					
				</animated.div>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={3} speed={.6}>
			<section>
				<animated.div class="wrap-75 center black-bk">
					
				</animated.div>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={0.85} speed={0.2}>
			<section>
				<div class="wrap-75 center column">
					<h3 class="text-center color-grey">I make</h3>
					<h2 class="text-center">Custom Websites</h2>
				</div>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={1.85} speed={0.2}>
			<section>
				<div class="wrap-75 center column">
					<h3 class="text-center color-grey">I help with</h3>
					<h2 class="text-center">Page Speed / SEO</h2>
				</div>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={2.85} speed={0.2}>
			<section>
				<div class="wrap-75 center column">
					<h3 class="text-center color-grey">kind of a</h3>
					<h2 class="text-center">Wordpress Janitor</h2>
				</div>
			</section>
		</ParallaxLayer>
		
		<ParallaxLayer offset={1} speed={0.4}>
			<section class="services-layer-1">
				<p class="text-center padding">Build anything from single page apps to full Wordpress sites</p>
			</section>
		</ParallaxLayer>

		<ParallaxLayer offset={2} speed={0.4}>
			<section class="services-layer-2">				
				<p class="text-center padding">Improve the marketability of your existing site</p>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={3} speed={.4}>
			<section class="services-layer-3">
				<p class="text-center padding">Wordpress Plugin/Theme Updates and Debugging</p>
			</section>
		</ParallaxLayer>
		</React.Fragment>
		)
}

export default Services