import React, { Component, useState } from "react"
import { Link } from "gatsby"

import { Parallax, ParallaxLayer } from 'react-spring/renderprops-addons'
import { animated, useSpring } from 'react-spring'



const Title = (props) => {

	const top = useSpring({
		to: {top: '0'},
		from: {top: '50px' }
	})

	const mid = useSpring({
		to: {top: '0'},
		from: {top: '50px' },
		delay: 500
	})

	const bottom = useSpring({
		to: {top: '0'},
		from: {top: '50px' },
		delay: 1000
	})

	return(
		<React.Fragment>

		<ParallaxLayer offset={0.25} speed={.4}>
			<section>
				<animated.div class="wrap-50 center column">
					<p class="scroll">Scroll</p>
				</animated.div>
			</section>
		</ParallaxLayer>



		<ParallaxLayer offset={0} speed={.6}>
			<section>
				<animated.div class="wrap-50 center black-bk">
					
				</animated.div>
			</section>
		</ParallaxLayer>
		


		<ParallaxLayer offset={0} speed={.2}>
			<section>
				<animated.div class="wrap-100 center">
					<h1 class="text-center">Front End Developer</h1>
				</animated.div>
			</section>
		</ParallaxLayer>


		<ParallaxLayer offset={0} speed={0.2}>
			<section>
				<animated.div class="wrap-75 top-center">
					<h3 class="color-grey text-center">Jeff Schwartz</h3>
					<h3 class="color-grey text-center">Hacker Supreme</h3>
				</animated.div>
			</section>
		</ParallaxLayer>



		</React.Fragment>
		)
}

export default Title