import React, { Component, useState } from "react"
import { Link } from "gatsby"

import { Parallax, ParallaxLayer } from 'react-spring/renderprops-addons'
import { animated, useSpring } from 'react-spring'

const Skills = (props) => {

	return(
		<React.Fragment>
		<ParallaxLayer offset={4} speed={.6}>
			<section>
				<animated.div class="wrap-75 center black-bk">
					
				</animated.div>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={3.75} speed={0.2}>
			<section>
				<h2 class="text-center">What my current <br/> skillset looks like:</h2>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={4} speed={0.4}>
			<section>
				<div class="row wrap-50 center">
					<p>HTML</p>
					<p>CSS</p>
					<p>Javascript</p>
					<p>React.js</p>
					<p>PHP</p>
					<p>jQuery</p>
					<p>Node.js</p>
					<p>Redux.js</p>
					<p>Gatsby.js</p>
				</div>
			</section>
		</ParallaxLayer>
		<ParallaxLayer offset={4.4} speed={.2}>
			<section class="skills-layer-1">
				<div class="row">
					<h3 class="color-grey">And More</h3>
				</div>
			</section>
		</ParallaxLayer>
		</React.Fragment>
		)
}

export default Skills